import Vuex from 'vuex'
import Vue from 'vue'
import Axios from 'axios'

Vue.use(Vuex);

export const store = new Vuex.Store({
    //this.$store.state.변수명 으로 접근
    state: {

    },

    //this.$store.getters.변수명 으로 접근
    getters:{
        // getCounter: function (state) {
        //     return state.counter;
        // }
    },

    //동기적 로직 수행 setter역할 
    //this.$store.commit('', payload)
    mutations:{
        // addCounter: function (state, payload) {
        //     state.counter = payload.value;
        // }
    },

    //비동기 로직 수행 this.$store.dispatch('addCounter');
    actions:{
        getWorksByYear:(context, payload)=>{
            return new Promise((resolve, reject)=>{                
                Axios.get('/work/', {
                    params: {
                        year: payload,
                    }
                })
                .then((res)=>{
                    resolve(res.data);
                })
                .catch((err)=>{
                    console.log(err)
                    reject(err);
                })
            });
        },

        uploadImg: (context, payload)=>{
            return new Promise((resolve, reject)=>{                
                Axios.post('/upload', payload, {
                    headers: {
                        'Content-Type': 'multipart/form-data'
                    }
                })
                    .then((res)=>{
                        resolve(res);
                    })
                    .catch((res)=>{
                        reject(res);
                    })
            });
        },
        createWork: (context, payload)=>{
            return new Promise((resolve, reject)=>{
                context.dispatch('uploadImg', payload.formData)
                    .then((res)=>{
                        console.log(res);

                        let newWork = {
                            title: payload.title,
                            material: payload.material,
                            year: payload.year,
                            height: payload.height,
                            width: payload.width,
                            url: res.data.url,
                        }
                        
                        console.log(newWork);

                        Axios.post('/work',newWork);
                    })
                    .then((res)=>{
                        resolve(res);
                    })
                    .catch((err)=>{
                        reject(err);
                    })
            });
        }
    },





})