import Vue from 'vue'
import App from './App.vue'
import vuetify from './plugins/vuetify';
import VueCarousel from 'vue-carousel';
import {router} from './routes'
import {store} from './store'

Vue.config.productionTip = false
Vue.use(VueCarousel);

new Vue({
  router,
  store,
  vuetify,
  render: h => h(App)
}).$mount('#app')
